.custom-loader {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: radial-gradient(farthest-side, #ffffff 94%, #0000) top/4px 4px
      no-repeat,
    conic-gradient(#0000 30%, #ffffff);
  -webkit-mask: radial-gradient(farthest-side, #0000 calc(100% - 4px), #000 0);
  animation: s3 1s infinite linear;
}

.loaderPrimary {
  background: radial-gradient(farthest-side, #ffffff 94%, #0000) top/4px 4px
      no-repeat,
    conic-gradient(#0000 30%, #ffffff);
}

.loaderSecondary {
  background: radial-gradient(farthest-side, #4964fe80 94%, #0000) top/4px 4px
      no-repeat,
    conic-gradient(#0000 30%, #4964fe80);
}

.loaderExtraSmall {
  width: 20px;
  height: 20px;
}

.loaderSmall {
  width: 25px;
  height: 25px;
}

.loaderMedium {
  width: 50px;
  height: 50px;
}

.loaderLarge {
  width: 75px;
  height: 75px;
}

.loaderSlow {
  animation: s3 2s infinite linear;
}

.loaderNormal {
  animation: s3 1s infinite linear;
}

.loaderFast {
  animation: s3 0.5s infinite linear;
}

@keyframes s3 {
  100% {
    transform: rotate(1turn);
  }
}

.loaderWrap {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  height: 100%;
  width: 100%;
  z-index: 99;
  display: flex;
  align-items: center;
  justify-content: center;
}

.opacityLevel {
  &_2 {
    background-color: #181e259e;
  }

  &_3 {
    background-color: var(--bg-color);
  }
}

